.table {
    position: relative;
    top: 65px;
    width: 100%;
    margin: 0 auto;
    float: none;
}

.wbdv-confirm-change {
    color: white;
}

.wbdv-confirm-change:hover {
    color: limegreen;
}

.wbdv-edit-row {
    color: white;
}

.wbdv-edit-row:hover {
    color: gold;
}

.wbdv-delete-row {
    color: white;
}

.wbdv-delete-row:hover {
    color: red;
}

.wbdv-select-row {
    background-color: #007bff;
    color: white;
}

.wbdv-file-text {
    margin-right: 0.75rem;
    color: #007bff;
    float: left;
}

.form-control {
    width: 80%;
    float: left;
}

.wbdv-course-title {
    position: relative;
}

.wbdv-course-title-select {
    color: white;
    position: relative;
    top: 7px;
}

.wbdv-file-text-select {
    margin-right: 0.75rem;
    color: white;
    float: left;
    position: relative;
    top: 7px;
}

.wbdv-row-hover:hover {
    background-color: rgba(0, 123, 255, 0.23);
}

.wbdv-last-modified-select {
    position: relative;
    top: 7px;
}

.wbdv-owned-by-select {
    position: relative;
    top: 7px;
}

@media (max-width: 768px) {
    .wbdv-last-modified {
        display:none;
    }

    .wbdv-last-modified-select {
        display:none;
    }
}

@media (max-width: 576px) {
    .wbdv-owned-by {
        display: none;
    }
    .wbdv-owned-by-select {
        display: none;
    }
}

