body {
    min-width:1000px;
}

.nav-pills .nav-link {
    border-radius: 0;
}

.wbdv-editor-header {
    padding-top: 15px;
    padding-bottom: 15px;
    background-color: #212529;
    color: white;
}

.container {
    max-width: 95%;
}

.wbdv-module-item {
    width: 100%;
    position: relative;
    display: block;
    border: 1px solid rgba(0,0,0,.125);
}

.list-group-item.active {
    z-index: 2;
    color: #fff;
    background-color: #c6c8ca;
    border-color: #c6c8ca;
}

.wbdv-module-item-tile {
    width: 100%;
    background-color: dimgrey;
    border-color: transparent;
}

.wbdv-module-item-tile:hover {
    background-color: #c6c8ca;
    width: 100%;
}

.wbdv-module-list-div {
    background-color: #212529;
}

.wbdv-lesson-item-add-btn-icon {
    color:white;
    background-color: transparent;
    border: transparent;
    position: relative;
    top: 25%;
}

.wbdv-lesson-item-add-btn-icon:hover {
    background-color: dimgrey;
}

.wbdv-module-section {
    margin-left: 25px;
    margin-right: 0;
}

.wbdv-module-item-add-btn {
    margin: .5rem .5rem;
}

.wbdv-close {
    color: lightgrey;
    position: relative;
    top: 30%;
    float: left;
}

.wbdv-close-icon:hover {
    color: red;
}

.wbdv-editor-course-title {
    color:white;
}

.wbdv-lesson-tabs, .wbdv-topic-tabs {
    background-color: dimgrey;
    color:white;
    margin: 5px;
    padding-left: 10px;
}

.nav-pills .nav-link.active, .nav-pills.show > .nav-link {
    color: #fff;
    background-color: dimgrey;
}

.nav-pills .nav-link {
    border-radius: 0;
}

.wbdv-lesson-list {
    padding-top: 10px;
    padding-bottom: 10px;
    width: 100%;
}

.wbdv-topic-pills {
    background-color: darkgray;
    color:white;
}

.nav-link:hover {
    color:white;
    background-color: dimgrey;
}

.wbdv-topic-pill-divs {
    padding: 10px;
}

.wbdv-module-item-add-btn-icon {
    color: lightgrey;
}

.preview {
    padding-left: 10px;
    padding-right:10px;
    margin-top: 14%;
}

.fa-toggle-off {
    position: relative;
    top: 20%;
}
.div-inline{
    display:inline-block;
}

.widget-box {
    border: 2px solid grey;
    padding: 10px 7px 10px 7px;
    border-radius: 5px;
}

.widget-sub {
    width: 95%;
    position: relative;
    left: 2.5%;
}

.widget-sub-border {
    border: 2px solid grey;
    border-radius: 5px;
}
.widget-sub-header {
    padding-left: 15px;
}

.buttons-in-main {
    width:98%;
}

.topic-widget-section {
    border-right: 2px solid grey;
    border-bottom: 2px solid grey;
    padding: 10px 7px 10px 7px;
}

.arrow {
    margin-right: 5px;
}

.wbdv-module-item-delete-btn {
    margin-left: 5px;
}

.heading-text {
    margin-top: 5px;
}

.wbdv-module-item-add-btn:hover {
    color: green;
}

.wbdv-module-item-add-btn {
    color: white;
}

.wbdv-editor-back {
    color: white;
}

.wbdv-editor-back:hover {
    color: red;
}

.wbdv-editor-add-lesson {
    color: white;
    position: relative;
    top: 10%;
}

.wbdv-editor-add-lesson:hover {
    color: green;
}

.wbdv-edit-module, .wbdv-edit-lesson, .wbdv-edit-topic{
    color: white;
    margin-top: -0.5rem;
}

.wbdv-edit-module:hover, .wbdv-edit-lesson:hover, .wbdv-edit-topic:hover {
    color: yellow;
}

.wbdv-add-module-btn {
    color: white;
}

.wbdv-add-module-btn:hover {
    color: green;
}

.wbdv-add-module {
    height: 52px;
}

.wbdv-delete-lesson, .wbdv-delete-topic, .wbdv-delete-module {
    color: white;
}

.wbdv-delete-lesson:hover, .wbdv-delete-topic:hover, .wbdv-delete-module:hover {
    color: red;
}

.wbdv-save-lesson, .wbdv-save-module, .wbdv-save-topic {
    color: white;
}

.wbdv-save-lesson:hover, .wbdv-save-module:hover, .wbdv-save-topic:hover {
    color: green;
}

.wbdv-add-lesson-btn {
    color: white;
}

.wbdv-add-lesson-btn:hover {
    color:green;
}




